body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.4;
  color: #222;
  font-family: "OpenSansRegular", sans-serif;
  overflow-x: hidden;
}

section {
  padding: 60px 0 80px;
}

section h2 {
  margin-top: 0;
  margin-bottom: 60px;
}

hr {
  margin: 0;
  border-top: 1px solid #a2a2a2;
}

.responsive-img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "OpenSansSemiBold", sans-serif;
  font-weight: 500;
}

h1 {
  font-family: "OpenSansLight", sans-serif;
  font-size: 3em;
  line-height: 1.2;
}

h2 {
  font-family: "OpenSansLight", sans-serif;
  font-size: 2.25em;
}

h3,
h4 {
  font-family: "OpenSansSemiBold", sans-serif;
  text-transform: uppercase;
}

h3 {
  font-size: 1.125em;
}

h4 {
  font-size: 1em;
}

a {
  text-decoration: underline;
  color: #f94b02;
  outline: none !important;
}

a:hover {
  text-decoration: none;
}

b,
strong {
  font-family: "OpenSansSemiBold", sans-serif;
  font-weight: normal;
}

.fa {
  color: #fe7f4a;
}

.button {
  display: inline-block;
  border: none;
  background-color: #f94b02;
  text-align: center;
  color: #fff;
  font-size: 0.875em;
  text-decoration: none;
  letter-spacing: 2px;
  text-transform: uppercase;
  padding: 13px 34px;
  border-radius: 3px;
  transition: all 0.15s ease;
}

.button:hover,
.button:focus {
  color: #fff;
  text-decoration: none;
}

.button:hover {
  background-color: #fd6d31;
}

.button:active {
  background-color: #c63c02;
  transform: translateY(2px);
}

.button-accent {
  background-color: transparent;
  border: 1px solid #f94b02;
  box-shadow: 0 4px 0 #db4202;
  color: #f94b02;
  font-family: "OpenSansSemiBold", sans-serif;
}

.button-accent:hover {
  background-color: #f94b02;
}

.button-accent:active,
.button-accent:focus {
  background-color: #db4202;
}

.button-small {
  padding: 9px 28px;
  font-size: 0.8125em;
  background-color: #f94b02;
}

.button-small:hover {
  background-color: #fd6627;
}

.button-small:active,
.button-small:focus {
  background-color: #db4202;
}

.button-gray {
  background-color: #e8e8e8;
  color: #666;
}

* {
  transition: filter .25s ease;
}

.mfp-wrap ~ * {
  filter: blur(5px);
}

.my-mfp-zoom-in .form-callback {
  opacity: 0;
  transform: scale(0.8);
}

.my-mfp-zoom-in.mfp-ready .form-callback {
  opacity: 1;
  transform: scale(1);
}

.my-mfp-zoom-in.mfp-removing .portfolio-popup,
.my-mfp-zoom-in.mfp-removing .form-callback {
  transform: scale(0.8);
  opacity: 0;
}

.my-mfp-zoom-in.mfp-bg {
  opacity: 0;
  transition: opacity 0.3s ease-out;
}

.my-mfp-zoom-in.mfp-ready.mfp-bg {
  opacity: 0.8;
}

.my-mfp-zoom-in.mfp-removing.mfp-bg {
  opacity: 0;
}

.form-callback {
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  max-width: 320px;
  margin: auto;
  box-shadow: rgba(0, 0, 0, 0.3) 0 0 20px;
}

.form-callback .mfp-close {
  color: #fff;
  opacity: .3;
  transition: all 0.25s ease;
}

.form-callback .mfp-close:hover {
  opacity: .6;
}

.form-callback .contact-form-head h4 {
  color: #fff;
}

.success {
  text-align: center;
  color: #222;
  background-color: transparent;
  position: relative;
  z-index: 1;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  transition: all 0.5s ease;
}

.success h4 {
  margin-bottom: 10px;
}

.success.active {
  display: block;
}

.main-head {
  color: #fafafa;
  position: relative;
  min-height: 90vh;
  background: #222 url("../img/bg.jpg") no-repeat center top;
  background-size: cover;
}

.main-head .top-logo img {
  width: 150px;
}

.main-head .top-line {
