  margin-top: 20px;
  float: left;
}

.s-about-item img {
  width: 150px;
  margin-bottom: 20px;
}

.s-features {
  color: #fafafa;
}

.s-features img {
  width: 150px;
  margin-bottom: 20px;
}

.s-works .col-md-3 {
  padding: 5px 5px;
}

.s-works .slide {
  position: relative;
  overflow: hidden;
  display: block;
}

.s-works .slide a {
  display: block;
}

.s-works .slide:hover .slide-content {
  opacity: 1;
  transform: scale(0.95);
}

.s-works .slide img {
  transition: all 0.5s ease;
}

.s-works .slide-content {
  position: absolute;
  color: #090909;
  padding-top: 10%;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: rgba(250, 250, 250, 0.65);
  text-align: center;
  transition: all 0.5s ease;
  transform: scale(1.1);
}

.s-works .slide-content .fa {
  color: #090909;
  font-size: 40px;
  padding: 20px 20px 5px;
}

.s-works h3 {
  text-transform: none;
  font-size: 1.25em;
}

.slide-popup {
  position: relative;
  background-color: #fff;
  max-width: 900px;
  margin: auto;
  text-align: center;
  display: block;
  transition: all 0.25s ease;
}

.slide-popup .col-sm-12 {
  padding: 0;
}

.s-contacts {
  background-color: #555555;
  color: #fafafa;
  padding-top: 60px;
}

.form {
  display: block;
  padding: 20px 30px;
  border: 1px solid #f94b02;
  border-radius: 8px;
  margin-bottom: 50px;
}

.form-head h3 {
  margin: 5px 0 15px;
}

.form-head p {
  margin-bottom: 25px;
}

.form label {
  display: block;
}

.form label span {
  display: block;
  color: rgba(250, 250, 250, 0.7);
