}

.form label input,
.form label textarea {
  display: block;
  width: 100%;
  outline: none;
  background-color: transparent;
  color: #fafafa;
  border: 1px solid #f94b02;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px 16px;
}

.form .button {
  margin-top: 20px;
}

.contacts-text h3 {
  margin: 20px 0 20px;
  text-transform: none;
}

.contacts-text p {
  font-size: 0.9375em;
  margin-bottom: 15px;
}

.contacts-text .fa {
  margin-right: 0;
  width: 15px;
  font-size: 20px;
}

.contacts-text a {
  text-decoration: none;
}

.contacts-text a .img-svg {
  margin-top: 20px;
  margin-right: 20px;
}

.contacts-text a:hover,
.contacts-text a:focus {
